export default function num2persian(input) {
    const ones = ['', '', '', '', '', '', '', '', '', ''];
    const tens = ['', '', '', '', '', '', '', '', '', ''];
    const hundreds = ['', '', '', '', '', '', '', '', '', ''];
    const thousands = ['', '', '', ''];

    if (isNaN(input)) return '';
    let num = parseInt(input, 10);
    if (num === 0) return '';

    let str = '';
    let k = 0;
    while (num > 0) {
        let part = num % 1000;
        if (part !== 0) {
            let partStr = '';
            let h = Math.floor(part / 100);
            let t = Math.floor((part % 100) / 10);
            let o = part % 10;
            if (h) partStr += hundreds[h] + '  ';
            if (t > 1) {
                partStr += tens[t] + (o ? '  ' + ones[o] : '');
            } else if (t === 1) {
                if (o === 0) partStr += '';
                else if (o === 1) partStr += '';
                else if (o === 2) partStr += '';
                else if (o === 3) partStr += '';
                else if (o === 4) partStr += '';
                else if (o === 5) partStr += '';
                else if (o === 6) partStr += '';
                else if (o === 7) partStr += '';
                else if (o === 8) partStr += '';
                else if (o === 9) partStr += '';
            } else if (o) {
                partStr += ones[o];
            }
            partStr = partStr.replace(/  $/, '');
            str = partStr + (thousands[k] ? ' ' + thousands[k] : '') + (str ? '  ' + str : '');
        }
        num = Math.floor(num / 1000);
        k++;
    }
    return str.trim();
} 